<template>
  <div class="content">
    <van-tabs v-model="activeTabs"  @change="changes" :sticky="sticky" :color="borderColor" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
      <van-tab  title="我的门店">
        <earningsLeft v-if="divShow"></earningsLeft>
      </van-tab>
      <van-tab title="下级代理商"  v-if="tabShow">
        <earningsRight v-if="!divShow"   ></earningsRight>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
  const earningsLeft = () => import("../../dataStat/Earnings/earningsLeft.vue");
  const earningsRight = () => import("../../dataStat/Earnings/earningsRight.vue");
  export default {
    components:{
      earningsLeft,
      earningsRight
    },
    data() {
      return {
        activeTabs:1,
        divShow:true,
        sticky:true,
        tabShow:false,
        borderColor:"rgba(0,0,0,0)"
      }
    },
    mounted(){
     if(this.$store.state.accountType=="1"){
        this.borderColor="rgb(255, 141, 89)";
        this.tabShow=true;
     }
    },
    methods:{
      changes(index){
        if(index==1){
            this.divShow=false;
        }else {
          this.divShow=true;
        }
      }
    },
  }
</script>

<style scoped>

.content{
   min-height: 100%;

}
</style>



